<template>
    <div v-loading="loading">
        <top-nav mdl="sucai" :select="0"></top-nav>
        <div class="wyu-main">
            <div class="wyu-block">
                <div v-show="!loading" class="wyu-tips wyu-tips-info" style="display: inline-block;width: 100%">
                    <el-row>
                        <el-col :span="21">
                            <el-checkbox :border="true" size="mini" v-model="checkAll" @change="selecrAll">全选
                            </el-checkbox>
                            <el-button size="mini"  @click="yidong=true,yiding_all_category_id=category_id" :disabled="checkAll==false" :type="checkAll ? 'primary' : 'info'">
                                批量移动
                            </el-button>
                            <el-button size="mini" @click="deleteAll(null)" :disabled="checkAll==false" :type="checkAll ? 'primary' : 'info'">
                                批量删除
                            </el-button>
                        </el-col>
                        <el-col :span="3">
                            <el-button size="mini" @click="AddPhoto" type="primary" icon="el-icon-upload">添加图片</el-button>
                        </el-col>
                        <el-col :span="24" style="margin-top: 10px;">
                            <span>分组：</span>
                            <el-button round @click="category_id = val.category_id;getRes()"
                                       :type="val.category_id == category_id ? 'primary' : ''" size="mini"
                                       v-for="(val,key) in  cats" :key="key">{{val.title}}({{val.num}})
                            </el-button>
                        </el-col>
                    </el-row>
                </div>
                <div style="clear: both"></div>
                <div style="display: inline-block;margin-top: 20px;height: 700px;overflow-y: auto">
                    <div class="sucai-photo-item" v-for="(val,index) in list" :key="index">
                        <img width="180" height="180" :src="val.photo">
                        <!--//复选框-->
                        <p class="ellipsis-01" style="margin:  2px 0;color: #8a8a8a;font-size: 12px;">
                            <el-checkbox style="width: 1px" @change="selectRes(val.resource_id)"
                                         :value="checkSelect(val.resource_id)"></el-checkbox>
                            <span>{{val.title}}</span>
                        </p>
                        <div class="sucai-photo-item-btn">
                            <el-row>
                                <el-col :span="8">
                                    <el-popover placement="top" width="200" v-model="val.is_delete">
                                        <div v-loading="val.is_delete_lod">
                                            <el-input v-model="val.title" size="mini"></el-input>
                                            <div style="text-align: right; margin: 0">
                                                <el-button size="mini" type="text" @click="val.is_delete = false">取消
                                                </el-button>
                                                <el-button type="text" size="mini" @click="editName(val)">确定
                                                </el-button>
                                            </div>
                                        </div>
                                        <el-button slot="reference" type="text" icon="el-icon-edit">编辑</el-button>
                                    </el-popover>
                                </el-col>
                                <el-col :span="8">
                                    <el-popover placement="top" width="200" v-model="val.is_delete1">
                                        <div v-loading="val.is_delete_lod1">
                                            <el-select v-model="val.category_id" size="mini" placeholder="请选择">
                                                <el-option
                                                        v-for="item in cats"
                                                        :key="item.category_id"
                                                        :label="item.title"
                                                        :value="item.category_id">
                                                </el-option>
                                            </el-select>
                                            <div style="text-align: right; margin: 0">
                                                <el-button size="mini" type="text" @click="val.is_delete1 = false">取消
                                                </el-button>
                                                <el-button type="text" size="mini" @click="editGroup(val)">
                                                    确定
                                                </el-button>
                                            </div>
                                        </div>
                                        <el-button type="text" slot="reference" icon="el-icon-sort">移动</el-button>
                                    </el-popover>
                                </el-col>
                                <el-col :span="8">
                                    <el-popover placement="top" width="200" v-model="val.is_delete2">
                                        <div v-loading="val.is_delete_lod2">
                                            <p style="text-align: left">确认删除吗？</p>
                                            <div style="text-align: right; margin: 0">
                                                <el-button size="mini" type="text" @click="val.is_delete2 = false">取消
                                                </el-button>
                                                <el-button type="text" size="mini" @click="deletePhoto(val)">确定
                                                </el-button>
                                            </div>
                                        </div>
                                        <el-button type="text" slot="reference" icon="el-icon-delete">删除</el-button>
                                    </el-popover>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </div>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getRes" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
        <!--批量移动弹出-->
        <el-dialog title="移动分组" :visible.sync="yidong" width="500px" :modal-append-to-body="false"	 center>
            <el-select v-model="yiding_all_category_id" size="mini" placeholder="请选择">
                <el-option
                        v-for="item in cats"
                        :key="item.category_id"
                        :label="item.title"
                        :value="item.category_id">
                </el-option>
            </el-select>
            <span slot="footer" class="dialog-footer">
              <el-button size="mini" @click="yidong = false">取 消</el-button>
             <el-button  size="mini" type="primary" @click="editAllGroup">确 定</el-button>
            </span>
        </el-dialog>

        <!--添加图片-->
        <el-dialog title="上传图片" :visible.sync="addPhoto" width="600px" :modal-append-to-body="false"	 center>
            <el-form  ref="form"  size="mini" label-width="80px" >
                <el-form-item label="分类">
                    <el-select v-model="add_all_category_id" size="mini" placeholder="请选择">
                        <el-option
                                v-for="item in cats"
                                :key="item.category_id"
                                :label="item.title"
                                :value="item.category_id">
                        </el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="上传图片" >
                    <el-upload class="uploadsucai" :on-exceed="tisLimit" ref="uploadphoto"	 :limit="10"  multiple :data="getParam()" list-type="picture-card" :action="getaction()">
                        <i class="el-icon-upload"></i>
                    </el-upload>
                </el-form-item>
                <el-form-item>
                    <el-button  size="mini" type="primary" @click="okAddPhoto">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import TopNav from "../../components/TopNav";
    import * as api from "@/api";
    import * as setting from '../../../public/config.js';
    export default {
        name: "SucaiIndex",
        components: {TopNav},
        data() {
            return {
                cats: [],
                category_id: -1,
                list: [],
                loading: false,
                datas: {},
                selectIds: [],
                checkAll: false,
                add_all_category_id:-1,
                yiding_all_category_id:-1,
                yidong:false,
                addPhoto:false,
            }
        },
        created() {
            this.getCatList();
            this.getRes();
        },
        methods: {
            AddPhoto(){
                if(this.$refs.uploadphoto){
                    this.$refs.uploadphoto.clearFiles();
                }
                this.addPhoto = true;
            },
            okAddPhoto(){
                this.addPhoto = false;
                this.getCatList();
                this.getRes(1)
            },
            getaction(){
                let host = '';
                if(process.env.NODE_ENV == "development"){
                    host = "https://open.woyaou.com/";
                }else{
                    host = "/";
                }
                return  host +'shop/admin.upload/imageUploadAll';
            },
            getParam(){
                return {
                    appToken:this.$store.getters.appToken,
                    groupId:this.add_all_category_id,
                }
            },
            selectRes(val) {
                if (this.checkSelect(val)) {
                    this.selectIds.splice(this.selectIds.indexOf(val), 1)
                } else {
                    this.selectIds.push(val);
                }
                this.checkAll = this.selectIds.length > 0;
            },

            checkSelect(val) {
                return this.selectIds.indexOf(val) >= 0;
            },
            tisLimit(){
                this.$message("单次最大上传10张图片")
            },
            //去选
            selecrAll() {
                if (this.checkAll == false) {
                    this.selectIds = [];
                } else {
                    this.list.forEach(val => {
                        this.selectIds.push(val.resource_id);
                    })
                }
            },

            getRes(page = 1) {
                this.loading = true;
                api.post("/shop/admin.resource.resource/index", {
                    appToken: this.$store.getters.appToken,
                    category_id: this.category_id,
                    limit: 24,
                    page: page,
                }).then(res => {
                    this.datas = res;
                    this.list = res.list;
                    //给与js 足够的处理时间
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
            getCatList() {
                api.post("/shop/admin.resource.category/getCatCount", {
                    appToken: this.$store.getters.appToken,
                }).then(res => {
                    this.cats = res.list;
                }).catch(() => {
                });
            },

            editName(row) {
                row.is_delete_lod = true;
                api.post("/shop/admin.resource.resource/editName", {
                    appToken: this.$store.getters.appToken,
                    id: row.resource_id,
                    name: row.title,
                }).then(() => {
                    row.is_delete_lod = false;
                    row.is_delete = false;
                }).catch(() => {
                    row.is_delete = false;
                    row.is_delete_lod = false;
                });
            },

            //批量删除
            deleteAll(){

                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                   this.deletePhoto();
                }).catch(() => {

                });
            },

            //删除
            deletePhoto(val = null) {
                if (!val && this.selectIds.length <= 0) {
                    return false;
                }
                this.loading = true;
                api.post("/shop/admin.resource.resource/delete", {
                    appToken: this.$store.getters.appToken,
                    id: val ? val.resource_id : 0,
                    ids: this.selectIds,
                }).then(() => {
                    this.getCatList();
                    this.getRes();
                    this.selectIds = [];
                    this.checkAll = true;
                }).catch(() => {
                    this.loading = false;
                    this.selectIds = [];
                });
            },

            //修改分组
            editAllGroup() {
                if(this.selectIds.length <=0){
                    return false;
                }
                this.loading = true;
                api.post("/shop/admin.resource.resource/editCat", {
                    appToken: this.$store.getters.appToken,
                    ids:this.selectIds,
                    cat_id: this.yiding_all_category_id,
                }).then(() => {
                    this.getCatList();
                    this.getRes();
                    this.selectIds = [];
                    this.yidong = false;
                    this.checkAll = true;
                }).catch(() => {
                    this.loading = false;
                });
            },

            //修改分组
            editGroup(val) {
                this.loading = true;
                api.post("/shop/admin.resource.resource/editCat", {
                    appToken: this.$store.getters.appToken,
                    id: val.resource_id,
                    cat_id: val.category_id,
                }).then(() => {
                    this.getCatList();
                    this.getRes();
                }).catch(() => {
                    this.loading = false;
                });
            },


        }
    }
</script>

<style scoped>
    .sucai-photo-item {
        width: 186px;
        height: 246px;
        border: 2px solid #f4f5f9;
        float: left;
        margin: 10px;
    }

    .sucai-photo-item-btn {
        background-color: #f4f5f9;
    }
</style>
